<script>
export default {
  name: 'App',
    data () {
    return {
      projectname: "e",
      actstyle:{backgroundColor:"rgba(0,0,0,0.1)"},
      navstyle:{},
      constyle:{},
      n_index:{},
      n_tech:{},
      n_roadmap:{},
      n_team:{},
      show:false,
      top:false,
      r1:false,
      r2:false,
      r3:false,
      r4:false,
      r5:false,
      r6:false,
      r7:false,
      team:false,
      adviser:false,
      coop:false,
      nav:{
        nav1:"nav1",
        nav2:"nav2",
        nav3:'nav3',
        dropnav1:"drop"
      },
      lang:'en-US'
    }
  },
  mounted()
  {
    window.addEventListener('scroll', this.handleScroll)
            setTimeout(() => {
                  this.top = true
                        }, 200);
      
  }
  ,
  methods:{
      handleScroll () {
        var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
        // console.log(scrollTop)

        if(scrollTop>350)
          this.top=false

        if(scrollTop<=200)
          this.top= true

        if (scrollTop >= 250)
          {
            this.show = true;
          }
        else
        {
          this.n_tech = {}
          this.n_team = {}
          this.n_roadmap = {}
          this.show = false;
        }
          
        if(scrollTop>=650)
        {
          this.n_tech = {boxShadow: "2px 2px 15px #999"}
          this.n_team = {}
          this.n_roadmap = {}
          this.actstyle = {backgroundColor:"",borderRadius:'0'};
          this.navstyle = {marginTop:'0px'}
          this.constyle = {width:'100%',paddingLeft:'0',paddingRight:'0'}
        }
        else 
        {
          this.n_tech = {}
          this.constyle = {}
          this.actstyle = {backgroundColor:"rgba(0,0,0,0.1)"};        
          this.navstyle = {marginTop:'20px'}
        }
        
        if(scrollTop>=1200)
        {
          this.r1 = true;
        }
        else 
          this.r1 = false;
        if(scrollTop>=1250)
        {
          this.r2 = true;
          this.n_tech = {}
          this.n_roadmap = {boxShadow: "2px 2px 15px #999"}
          this.n_team = {}
        }
        else 
          this.r2 = false;
        if(scrollTop>=1300)
          this.r3 = true;
        else 
          this.r3 = false;      
        if(scrollTop>=1400)
          this.r4 = true;
        else 
          this.r4 = false;  
        if(scrollTop>=1450)
          this.r5 = true;
        else 
          this.r5 = false;   
        if(scrollTop>=1500)
          this.r6 = true;
        else 
          this.r6 = false;   
        if(scrollTop>=1550)
          this.r7 = true;
        else 
          this.r7 = false;          
        if(scrollTop>=2000)
        {
          this.team = true;
          this.n_tech = {}
          this.n_roadmap = {}
          this.n_team = {boxShadow: "2px 2px 15px #999"}
        }
        // else 
        //   this.team = false;    
        // if(scrollTop>=3100)
        //   this.adviser = true;
        // else 
        //   this.adviser = false;         
        // if(scrollTop>=3900)
        //   this.coop = true;
        // else 
        //   this.coop = false;                              
      },
      changeLangEvent(id) {
          if ( id === 0 ) {
            this.lang = 'en-US';
            this.$i18n.locale = this.lang;
          }else {
            this.lang = 'zh-CN';
            this.$i18n.locale = this.lang;
          }
        },

  }
}
</script>

